.ad-box {
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;

    @include m('with-text') {
        padding-top: 25px;

        &::before {
            content: $ad-box-text;
            position: absolute;
            top: 0;
            right: 0;
            z-index: 2;
            font-size: $font-size-xs;
            font-weight: $font-weight-light;
            color: $gray-500;
            text-align: center;
            width: 100%;
            height: 25px;
            line-height: 25px;
        }
    } 

    @include m('majorboard') {
        width: $majorboard-sm-width;
        min-height: $majorboard-sm-height;

        @include media-breakpoint-up(md) {
            width: $majorboard-lg-width;
            min-height: $majorboard-lg-height;
        }
    }

    @include m('medium-rectangle') {
        min-width: $medium-rectangle-lg-width;
    }
}

.sp-ad {
    padding-bottom: $spacer;

    @include media-breakpoint-down(md) {
        display: none;
    }
}