.bigbanner-ad {
    background-color: $white;
    display: flex;
    justify-content: center;
    position: relative;
    z-index: $zindex-content;
    margin: 0 auto;
    padding: ($grid-gutter-width) 0;
    @include make-container-max-widths();

    @include e('content') {
        position: relative;
        overflow: hidden;
        
        @include m('large') {
            min-width: $bigbanner-large-sm-width;
            min-height: $bigbanner-large-sm-height;

            @include media-breakpoint-up(md) {
                min-width: $bigbanner-large-md-width;
                min-height: $bigbanner-large-md-height;
            }

            @include media-breakpoint-up(lg) {
                min-width: $bigbanner-large-lg-width;
                min-height: $bigbanner-large-lg-height;
            }
        }

        @include m('small') {
            min-width: $bigbanner-small-sm-width;
            min-height: $bigbanner-small-sm-height;

            @include media-breakpoint-up(md) {
                min-width: $bigbanner-small-md-width;
                min-height: $bigbanner-small-md-height;
            }

            @include media-breakpoint-up(lg) {
                min-width: $bigbanner-small-lg-width;
                min-height: $bigbanner-small-lg-height;
            }
        }
    }
}