// Special Box Type A

.ad-box-a {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    margin: ($grid-gutter-width * 1.5) 0;
    padding: ($grid-gutter-width * 5) 0 ($grid-gutter-width * 2);

    @include media-breakpoint-up(md) {
        padding: ($grid-gutter-width * 6) 0 ($grid-gutter-width * 3);
    }

    @include media-breakpoint-up(lg) {
        padding: ($grid-gutter-width * 9) 0 ($grid-gutter-width * 3);
    }

    @include e('link') {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

// Special Box Type B

.ad-box-b {
    background-color: $dark;
    margin: ($grid-gutter-width) auto;
    @include make-container-max-widths();

    .ad-banner {
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        display: block;
        width: 100%;
        height: 100%;
        min-height: 150px;
    }

    .box {
        background-color: $dark;

        h3,
        a {
            color: $white;
        }
    }
}