.sponsor {
    position: relative;

    &::before {
        content: $sponsor-text;
        background-color: rgba(0, 0, 0, 0.5);
        position: absolute;
        top: 0;
        right: 0;
        z-index: 2;
        font-size: $font-size-xs;
        font-weight: $font-weight-light;
        color: $white;
        padding: 2px 5px;
    }
}

.content-sponsor-5 {
    @include media-breakpoint-down(sm) {
        order: 5;
    }

    @include media-breakpoint-down(md) {
        order: 6;
    }
}

.content-sponsor-7 {
    @include media-breakpoint-down(sm) {
        order: 8;
    }
}