.ad-background {
    position: absolute;
    top: 0;
    left: 50%;
    z-index: $zindex-background;
    min-width: $background-sm-width;
    min-height: $background-sm-height;
    margin-left: $background-sm-width / 2 * -1;

    @include media-breakpoint-up(sm) {
        min-width: $background-sm-h-width;
        min-height: $background-sm-height;
        margin-left: $background-sm-h-width / 2 * -1;
    }

    @include media-breakpoint-up(md) {
        min-width: $background-md-width;
        min-height: $background-md-height;
        margin-left: $background-md-width / 2 * -1;
    }

    @include media-breakpoint-up(xm) {
        min-width: $background-md-h-width;
        min-height: $background-md-height;
        margin-left: $background-md-h-width / 2 * -1;
    }

    @include media-breakpoint-up(lg) {
        min-width: $background-lg-width;
        min-height: $background-lg-height;
        margin-left: $background-lg-width / 2 * -1;
    }
}