.search-suggestion {
    background-color: #fff;
    position: absolute;
    z-index: 20;

    ul,
    li {
        list-style: none;
        padding: 0;
    }

    a {
        display: block;
        text-decoration: none;
        padding: $spacer * .5;

        &:hover {
            background-color: $gray-100;
        }
    }

    @include m('keyword') {
        color: $primary;
        text-decoration: underline;
    }
}