.back2top-box {
    position: fixed;
    right: 15px;
    z-index: 15;

    @include m('with-sticky') {
        right: ($sticky-width + 30px);
    }

    .btn {
        width: 50px !important;
        height: 50px !important;

        small {
            display: block;
            margin-top: -6px;
        }
    }
}