.highlight-item {
    position: relative;
    display: flex;
    height: 100%;
    padding-bottom: 0;

    @include e('thumbnail') {
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
        height: 210px;

        a {
            width: 100%;
            height: 100%;
        }

        @include media-breakpoint-up(md) {
            height: 300px;
        }

        @include media-breakpoint-up(lg) {
            height: 422px;
        }
    }

    @include e('body') {
        background: -moz-linear-gradient(top, rgba(30,87,153,0) 0%, rgba(0,0,0,0.9) 100%);
        background: -webkit-linear-gradient(top, rgba(30,87,153,0) 0%,rgba(0,0,0,0.9) 100%);
        background: linear-gradient(to bottom, rgba(30,87,153,0) 0%,rgba(0,0,0,0.9) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#001e5799', endColorstr='#e6000000',GradientType=0 );
        position: absolute;
        bottom: 0;
        z-index: 1;
        color: $white;
        width: 100%;
        padding: $spacer;

        @include media-breakpoint-up(lg) {
            background-color: $dark;
            position: relative;
            width: 280px;
            min-width: 280px;
            padding: ($spacer * 1.5);
        }
    }

    @include e('title') {
        font-size: 1.125rem;
        font-weight: lighter;
        line-height: 1.5rem;
        text-align: center;
        margin-bottom: ($spacer *.25);

        a {
            color: $white;
            &:hover {
                color: $primary;
                text-decoration: none;
            }
        }

        @include media-breakpoint-up(md) {
            font-size: 1.5rem;
            line-height: 1.75rem;

            a {
                display: -webkit-box;  
                overflow: hidden;
                -webkit-box-orient: vertical;
                word-wrap: break-word;
                -webkit-line-clamp: 3;
                max-height: 134px;
            } 
        }

        @include media-breakpoint-up(lg) {
            text-align: left;
            
            a {   
                -webkit-line-clamp: 4;
                max-height: 134px;
            }   
        }
    }

    @include e('desc') {
        display: none;
        margin-bottom: 0;

        a {
            color: $gray-500;

            &:hover {
                color: $gray-500;
                text-decoration: none;
            }
        }

        @include media-breakpoint-up(lg) {
            display: -webkit-box;
            max-height: 143px;
            overflow: hidden;
            -webkit-box-orient: vertical;
            word-wrap: break-word;
            -webkit-line-clamp: 4;
        }

        @include media-breakpoint-up(lg) {
            -webkit-line-clamp: 6;
            max-height: 143px;
        }
    }

    @include e('viewmore') {
        display: none;

        @include media-breakpoint-up(lg) {
            display: block;
            position: absolute;
            bottom: 55px;
            right: ($spacer * 1.5);
        }
    }

    @include e('stat') {
        position: absolute;
        top: 0;
        right: 0;
        width: 25px;
        height: 25px;
    }
}