.footer-content {
    background-color: $dark;
    overflow: hidden;

    h4 {
        color: $white;
        text-transform: uppercase;
        margin-bottom: $spacer;
    }

    a {
        color: $gray-500;

        &:hover {
            color: lighten($gray-500, 15%);;
        }
    }

    @include e('go2top') {
        margin: 0 ($spacer * -1);

        @include media-breakpoint-up(md) {
            display: none;
        }
    }

    @include e('info') {
        @include media-breakpoint-up(lg) {
            display: none;
        }
    }

    @include e('brand') {
        text-align: center;
        padding: 45px 0;
    }

    @include e('social') {
        font-size: 1.25rem;
        text-align: center;
        padding-bottom: 1.25rem;

        a {
            color: $white;
        }
    }

    @include e('contact') {
        margin-left: $spacer;
        margin-right: $spacer;
        padding-bottom: ($spacer * 1.5);
    }

    @include e('menu') {
        display: none;
        margin: 0 auto;
        padding: 2.25rem 0;
        @include make-container-max-widths();

        @include media-breakpoint-up(lg) {
            display: flex;
        }
    }

    @include e('copyright') {
        font-size: 0.875rem;
        color: $gray-600;
        text-align: center;
        border-top-width: 0;
        border-top-style: solid;
        border-top-color: $gray-700;
        margin: 0 auto;
        padding: $spacer;
        @include make-container-max-widths();

        @include media-breakpoint-up(lg) {
            border-top-width: 1px;
        }
    }
}

.footer-logo {
    width: 80px;
    margin: 45px auto;
}