
.title-content {
    line-height: 2.5rem;
    overflow: hidden;
    height: 4.75rem;

    &.full-title {
        height: auto;
    }
}

.show-title {
    a {
        background: url("../images/angle-down-white.svg") no-repeat center;
        background-size: 16px;
        display: block;
        width: 45px;
        height: 45px;

        &.active {
            background-image: url("../images/angle-up-white.svg");
        }

        &:hover {
            background-color: lighten($dark, 10%);
        }
    }
}

.text-content{
    line-height: $line-height-base;
    overflow: hidden;
    height: 11.8rem;
    
    &.full-text{
        height: auto;
        margin-bottom: 25px;
    }
}

.show-more {
    position: relative;

    &::before {
        content: '';
        background: -moz-linear-gradient(top, rgba(30,87,153,0) 0%, rgba(255,255,255,1) 100%);
        background: -webkit-linear-gradient(top, rgba(30,87,153,0) 0%,rgba(255,255,255,1) 100%);
        background: linear-gradient(to bottom, rgba(30,87,153,0) 0%,rgba(255,255,255,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#001e5799', endColorstr='#ffffff',GradientType=0 );
        position: absolute;
        top: -25px;
        left: 0;
        width: 100%;
        height: 25px;
    }
}