.box {
    background-color: $white;
    position: relative;
    z-index: $zindex-content;
    width: 100%;
    margin: 0 auto;
    padding-right: $spacer;
    padding-left: $spacer;
    @include make-container-max-widths();

    + .box {
        border-top: 1px solid $border-color;
        margin-top: ($spacer * 1.5);
        padding-top: ($spacer * 1.5);
    }

    h3 {
        margin-bottom: 0;
    }

    @include m('full') {
        max-width: 100%;
        padding: ($spacer * 2.5) 0 !important;

        + .box {
            border-top: none !important;
            margin-top: 0;
        }
    }

    @include m('with-aside') {
        display: flex;
        flex-direction: column;

        @include media-breakpoint-up(md) {
            // min-height: 750px;
            flex-direction: row;
        }

        .box {
            padding-right: 0;
            padding-left: 0;
        }
    }

    @include m('inside') {
        padding: 0 0 !important;
    }

    @include m('lg-widget') {
        + .box {
            @include media-breakpoint-up(lg) {
                border-top: none !important;
                margin-top: 0 !important;
            }
        }
    }

    @include e('header') {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: $spacer;

        @include m('bg') {
            padding: ($spacer / 2) ($spacer);
            margin-left: ($spacer * -1);
            margin-right: ($spacer * -1);
        }
    }

    @include e('body') {
        width: 100%;
    }

    @include e('side-ad') {
        position: relative;
        width: $majorboard-lg-width;
        min-width: $majorboard-lg-width;
        overflow: hidden;
        margin: 0 auto;

        @include media-breakpoint-up(md) {
            margin-left: $spacer;
            margin-bottom: $spacer;
        }

        @include media-breakpoint-down(sm) {
            display: flex;
            justify-content: center;
            width: 100%;
            min-width: 100%;
        }
    }

    @include m('search-content') {
        @include media-breakpoint-down(sm) {
            margin-left: ($spacer * -1);
            margin-right: ($spacer * -1);
        }

        @include media-breakpoint-up(md) {
            border: 1px solid $border-color;
        }
    }
}