.featured-box {
    position: relative;
    z-index: 5;
    margin: 0 auto;
    padding: 0 $spacer;
    @include make-container-max-widths();

    @include media-breakpoint-up(md) {
        padding: 0 0;
    }

    @include m('video') {
        display: flex;
        flex-direction: row;
    }

    @include e('img') {
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        height: 220px;
        margin-bottom: 0;

        @include media-breakpoint-up(md) {
            height: 360px;
        }

        @include media-breakpoint-up(lg) {
            height: 550px;
        }
    }

    @include e('body') {
        background-color: $dark;
        width: 100%;
    }

    @include e('ad') {
        background-color: $dark;
        display: none;

        @include media-breakpoint-up(lg) {
            display: block;
            padding: $spacer;
        }
    }

    @include e('desc') {
        background-color: $dark;
        padding: $spacer;

        h1 {
            font-size: ($h1-font-size / 1.5);
            color: $white;
            margin: 0;

            @include media-breakpoint-up(md) {
                font-size: ($h1-font-size / 1.25);
            }

            @include media-breakpoint-up(lg) {
                font-size: $h1-font-size;
            }
        }
    }

    @include e('meta') {
        color: $gray-500;
        margin-top: 10px;
    }
}