.entry {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    word-wrap: break-word;
    padding-bottom: $spacer;

    @include e('thumb') {
        position: relative;
        height: auto;
        margin-bottom: $spacer;

        @include media-breakpoint-up(lg) {
            margin-bottom: $spacer;
        }

        a {
            display: block;
            position: relative;
        }

        img {
            width: 100%;
            max-width: 100%;
            height: auto;
        }

        @include m('zoom') {
            @include media-breakpoint-up(lg) {
                overflow: hidden;

                img {
                    transform: scale(1);
                    transition: .3s ease-in-out;

                    &:hover {
	                    transform: scale(1.1);
                    }
                }
            }
        }
    }

    @include e('body') {
        flex: 1 1 auto;  
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        overflow: hidden;
    }

    @include e('title') {
        width: 100%;
        margin-bottom: 0;

        a {
            position: relative;
            display: block;
            max-height: $entry-title-height;
            overflow: hidden;
        }

        @supports (-webkit-line-clamp: $entry-title-line-clamp) {
            a {
                display: -webkit-box;
                text-overflow: ellipsis;
                overflow: hidden;
                -webkit-line-clamp: $entry-title-line-clamp;
                -webkit-box-orient: vertical;

                &::after {
                    display: none;
                }
            }
        }

        @include media-breakpoint-down(sm) {
            font-size: $font-size-sm;

            a {
                max-height: ($entry-title-height - 9);
            }
        }
    }

    @include e('desc') {
        font-size: $font-size-sm;
        color: $gray-600;
        width: 100%;
        max-height: $entry-desc-height;
        overflow: hidden;
        margin-top: ($spacer / 1.5);
        margin-bottom: 0;

        @supports (-webkit-line-clamp: $entry-title-line-clamp) {      
            display: -webkit-box;
            text-overflow: ellipsis;
            overflow: hidden;
            -webkit-line-clamp: $entry-desc-line-clamp;
            -webkit-box-orient: vertical;

            &::after {
                display: none;
            }  
        }

        @include media-breakpoint-down(lg) {
            display: none;
        }
    }

    @include e('meta') {
        width: 100%;
        margin-top: auto;
        padding-top: ($spacer / 2);

        @include e('count') {
            font-size: $font-size-sm;
            color: $gray-500;
            margin-right: ($spacer * .5);

            @include media-breakpoint-down(md) {
                display: none;
            }
        }

        @include e('posted') {
            font-size: $font-size-sm;
            color: $gray-500;
        }
    }

    @include e('bottom') {
        width: 100%;
        margin-top: auto;
        padding-top: ($spacer / 2);

        @include e('price') {
            color: $red;
            margin-bottom: ($spacer / 2);
        }
    }

    @include m('hor') {
        flex-direction: row;

        .entry__thumb {
            flex: 0 0 40%;
            height: 100%;
            margin-right: $spacer;
            margin-bottom: 0;
        }
        
        .entry__title {
            font-size: $font-size-base !important;

            a {
                max-height: ($entry-title-height);
            }
        }

        .entry__meta {
            margin-top: 0;
        }
    }

    @include m('hor-sm-only') {
        flex-direction: row;

        .entry__thumb {
            flex: 0 0 40%;
            height: 100%;
            margin-right: $spacer;
            margin-bottom: 0;
        }

        .entry__title {
            font-size: $font-size-base !important;

            a {
                max-height: ($entry-title-height);
            }
        }

        .entry__meta {
            margin-top: 0;
        }

        @include media-breakpoint-up(md) {
            flex-direction: column;

            .entry__thumb {
                flex: 0 0 auto;
                height: auto;
                margin-right: 0;
                margin-bottom: $spacer;
            }

            .entry__meta {
                margin-top: auto;
            }
        }
    }
}

.player-overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 5;
    width: 52px;
    height: 52px;
    font-size: 3.25rem;
    color: $white;
    opacity: .5;
    vertical-align: middle;
    margin-top: -26px;
    margin-left: -26px;

    .svg-inline--fa {
        vertical-align: top;
    }
}