.top-item {
    position: relative;
    display: flex;
    align-items: center;
    border: transparent;
    border-width: 0;
    border-bottom-width: 1px;
    border-style: solid;
    border-color: $border-color;
    margin-left: ($spacer * -1);
    margin-right: ($spacer * -1);
    padding: $spacer;

    &.sponsor {
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;

        a {
            color: $white;
        }
    }

    a {
        &:hover {
            text-decoration: none;
        }
    }

    @include e('thumnb') {
        position: relative;
        width: 75px;
        min-width: 75px;
        height: 75px;
        margin-bottom: 0;
    }

    @include e('desc') {
        margin: 0 $spacer;

        h4 {
            margin-bottom: 0;
        }
    }

    @include e('aside') {
        width: 100px;
        min-width: 100px;
        margin-left: auto
    }

    @include media-breakpoint-up(md) {
        border-width: 1px;
        margin: 7.5px 0;
    }
}