.headline {
    background-color: $white;
    position: relative;
    z-index: 5;
    color: $primary;
    text-align: center;
    margin:  auto;
    padding: 1.25rem;
    @include make-container-max-widths();

    @include e('name') {
        background: #fff;
        display: inline-block;
		position: relative;
        z-index: 5;  
        margin-bottom: 0;
        padding: 0 .75rem;

        a {
            color: $primary;

            &:hover {
                text-decoration: none;
            }
        }
    }

    &:after {
        content: " ";
        display: block;
        background: $gray-200;
        position: absolute;
        top: 50%;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 8px;
        margin-top: -4px;
    }
}

.section-header {
    background-color: $white;
    position: relative;
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin:  auto;
    padding: $spacer;
    @include make-container-max-widths();

    h2 {
        margin-right: auto;
        margin-bottom: 0;
    }
}

.headline,
.section-header {
    transition: max-width .25s;

    @include m('sticky') {
        z-index: 10;

        @include media-breakpoint-up(xs) {
            &.is_stuck {
                left: 0;
                right: 0;
                width: 100% !important;
                max-width: 100% !important;
            }
        }
    }

    &.is_stuck {
        border-bottom: 1px solid $border-color;
        box-shadow: 0 1px 2px rgba(0,0,0,.1);
    }
}