.search-header {
    margin: 0 auto;
    padding: ($spacer);
    @include make-container-max-widths();

    h1 {
        margin-bottom: 0;
    }

    @include e('result') {
        color: $gray-500;
    }
}

.search-content {
    .tab-pane {
        @include media-breakpoint-up(md) {
            display: block;
            opacity: 1;
        }
    }
}