.topbar {

    // Topbar Sub

    @include e('sub') {
        background-color: $dark;
        position: fixed;
        z-index: 20;
        left: 0;
        top: 0;
        width: 100%;
        height: $topbar-height;
        opacity: 0;
        transition-duration: 0.3s;
        transform: translateY($topbar-height * -1);  

        @include media-breakpoint-up(lg) {
            position: relative;
            display: flex;
            align-items: center;
            font-size: 0.875rem;
            color: $white;
            opacity: 1;
            padding-right: $spacer;
            padding-left: $spacer;
            transform: translateY(0px);  
        }
    }

    @include e('right-side') {
        width: 100%;
        margin-left: 0;

        @include media-breakpoint-up(lg) {
            width: auto;
            margin-left: auto;
        }
    }

    @include e('search') {
        display: inline-block;
        position: relative;
        width: 100%;
        min-width: 250px;
        padding: 10px 15px;

        @include media-breakpoint-up(lg) {
            width: auto;
        }
    }

    @include e('social') {
        display: block;
        text-align: center;
        padding-top: 20px;

        h5 {
            display: none;
            font-size: $font-size-sm;
            margin-right: 5px;
            margin-bottom: 0;
        }

        a {
            display: inline-block;
            color: $secondary;
            padding: 10px;
            @include transition($btn-transition);

            &:hover {
                &.tp-fb {
                    color: theme-color("fb");;
                }

                &.tp-yt {
                    color: theme-color("yt");;
                }

                &.tp-ln {
                    color: theme-color("ln");;
                }

                &.tp-ig {
                    color: theme-color("ig");;
                }

                &.tp-tw {
                    color: theme-color("tw");;
                }
            }
        }

        @include media-breakpoint-up(xm) {
            display: inline-block;
            color: $white;
            padding-top: 0;

            a {
                color: $white;
            }
        }

        @include media-breakpoint-up(xl) {
            h5 {
                display: inline-block;
            }
        }
    }

    // Topbar Main
    
    @include e('main') {
        background-color: $white;
        display: flex;
        align-items: center;
        border-bottom: 1px solid $border-color;
        height: $topbar-height;
        padding-right: 0;
        padding-left: 0;

        @include media-breakpoint-up(xm) {
            padding-right: $spacer;
            padding-left: $spacer;
        }
    }

    @include e('mobile-nav') {
        background-color: transparent;
        position: relative;
        border: none;
        font-size: 1.25rem;
        width: $topbar-height;
        height: $topbar-height;
        line-height: $topbar-height;
        padding: 0;

        @include media-breakpoint-up(lg) {
            display: none;
        }
    }

    @include e('brand') {
        display: inline-block;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 0;

        a {
            display: flex;
            align-items: center;

            &:hover {
                text-decoration: none;
            }
        }

        @include e('name') {
            display: none;   
            font-family: $font-family-sukhumvit-bold;
            text-transform: uppercase;
            color: #000;
            vertical-align: middle;
            margin-left: 10px;
            margin-bottom: 0;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;

            span {
                color: $primary;
            }

            @include media-breakpoint-up(xl) {
                display: inline-block;
            }
        }

        @include media-breakpoint-up(lg) {
            margin-left: 0;
            margin-right: 15px;
        }

        @include e('logo') {
            display: inline-block;
            vertical-align: middle;
            width: 40px;
        
            path {
                fill: $primary;
            }
        }
    }

    @include e('sidemenu') {
        background-color: $white;
        position: fixed;
        left: 0;
        top: 0;
        bottom: 0;
        z-index: 15;
        width: $main-nav-width;
        height: $main-nav-height;
        opacity: 0;
        transition-duration: 0.3s;
        transform: translateX($main-nav-width * -1);
        -webkit-overflow-scrolling: touch;

        @include media-breakpoint-up(lg) {
            position: relative;
            display: inline-block;
            width: auto;
            height: auto;
            opacity: 1;
            transform: translateX(0px);
        }
    }

    @include e('menu') {
        font-size: 0;
    
        a {
            position: relative;
            display: inline-block;
            font-size: 1.125rem;
            text-decoration: none;
            padding: ($nav-link-padding-y + .25) ($nav-link-padding-x - .5);
            @include transition($btn-transition);

            &::after {
                content: '';
                background-color: $primary;
                position: absolute;
                left: 0;
                bottom: 0;
                width: 100%;
                height: 4px;
                opacity: 0;
                transition: opacity 0.3s, transform 0.3s;
                transform: translateY(10px);
            }
            
            &.active,
            &:hover {
                color: lighten($link-color, 30%);

                &::after {     
                    opacity: 1;
                    transform: translateY(0);
                }
            }

            @include media-breakpoint-down(xm) {
                display: block;   
                overflow: hidden;
                padding: $nav-link-padding-y $nav-link-padding-x;

                &.active,
                &:hover {
                    background-color: $dark;
                    color: $white;
                }

                &::after {
                    display: none;
                }
            }
        }
    }

    @include e('member') {
        font-size: 0;
        border-bottom: 1px solid $border-color;
        transition-duration: 0.3s;
        margin-left: 0;
        padding: $nav-link-padding-y $nav-link-padding-x;

        @include m('hidden') {
            display: none;
        }
        
        @include media-breakpoint-up(lg) {
            border-bottom: none;
            margin-left: auto;
            padding: 0 0;
        }
    }
}
  
@include media-breakpoint-down(lg) { 
    .wt--sidebar {
        .topbar__sidemenu {
            transform: translateX(0);
            opacity: 1;
            overflow-y: scroll;
        }

        .overlay-bg {
            display: block;
            opacity: 1;
        }
    }

    .wt--search {
        .topbar__sub {
            transform: translateY(0);
            opacity: 1;
        }

        .overlay-bg {
            display: block;
            opacity: 1;
        }
    }
}