.btn-sm-circle {
    font-size: 1.25rem;
    width: 40px;
    height: 40px;
    line-height: 35px;
    border-radius: 50%;

    span {
        display: none;
    }

    @include media-breakpoint-up(md) {
        font-size: .875rem;
        width: auto;
        height: auto;
        line-height: normal;
        border-radius: 0;

        span {
            display: inline-block;
        }
    }
}