.member-box {
    display: inline-block;
    font-size: 1rem;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;  
    line-height: 1.5;
    cursor: pointer;

    &:hover {
        text-decoration: none;
    }

    @include e('thumb') {
        display: inline-block;
        vertical-align: middle;
        width: 40px;
        height: 40px;
        overflow: hidden;
        margin-right: 10px;
    }

    @include e('name') {
        display: inline-block;
        vertical-align: middle;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        -o-text-overflow: ellipsis;
        -ms-text-overflow: ellipsis;
        max-width: 100px;
    }
}