.hero {
    position: relative;
    z-index: 5;
    margin: auto;
    @include make-container-max-widths();

    @include media-breakpoint-down(sm) {
        padding-left: $spacer;
        padding-right: $spacer;
        padding-bottom: $spacer;
    }

    @include e('top') {
        @include media-breakpoint-down(md) {
            display: none;
        }
    }

    @include e('header') {
        padding-left: $spacer;
        padding-right: $spacer;
        padding-top: $spacer * .5;
        padding-bottom: $spacer * .5;

        h1 {
            color: $white;
            margin-bottom: 0;
        }
    }
}