.sticky-ad {
    display: none;
    position: fixed;
    right: 15px;
    bottom: 0;
    width: $sticky-width;
    height: $sticky-height;
    z-index: 99999;

    @include e('content') {
        position: relative;
    }

    @include media-breakpoint-up(lg) {
        display: block;
    }

    .close-ad {
        display: none;
        height: 25px;
    }
}