.main-body {
    position: relative;
    overflow: hidden;
    padding-top: 85px;
    padding-bottom: 24px;

    @include m('normal') {
        padding-top: 24px !important;
    }

    @include media-breakpoint-up(md) {
        padding-top: 100px;
    }
      
    @include media-breakpoint-up(lg) {
        padding-top: 150px;
    }
}

.link_item {
    cursor: pointer;
}