.scroll-line {
    position: relative;
    z-index: 5;
    color: $white;
    text-align: center;
    margin:  auto;
    padding: 5px 0;
    @include make-container-max-widths();

    @include e('name') {
        background: $dark;
        display: inline-block;
		position: relative;
        z-index: 5;
        font-size: .75rem;
        width: 100px;
        cursor: pointer;
        margin-bottom: 0;
        padding: .1rem .75rem;
    }

    &:after {
        content: " ";
        display: block;
        background: $dark;
        position: absolute;
        top: 50%;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 1px;
        margin-top: -.5px;
    }
}