// stylelint-disable value-keyword-case

@font-face {
  font-family: 'Sukhumvit-Bold';
  src: url('../fonts/sukhumvitset-bold.eot');
  src: url('../fonts/sukhumvitset-bold.eot?#iefix') format('embedded-opentype'),
       url('../fonts/sukhumvitset-bold.woff2') format('woff2'),
       url('../fonts/sukhumvitset-bold.woff') format('woff'),
       url('../fonts/sukhumvitset-bold.ttf') format('truetype'),
       url('../fonts/sukhumvitset-bold.svg#sukhumvit_setbold') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Sukhumvit-Text';
  src: url('../fonts/sukhumvitset-text.eot');
  src: url('../fonts/sukhumvitset-text.eot?#iefix') format('embedded-opentype'),
       url('../fonts/sukhumvitset-text.woff2') format('woff2'),
       url('../fonts/sukhumvitset-text.woff') format('woff'),
       url('../fonts/sukhumvitset-text.ttf') format('truetype'),
       url('../fonts/sukhumvitset-text.svg#sukhumvit_settext') format('svg');
  font-weight: normal;
  font-style: normal;
}

$font-family-sukhumvit-bold: "Sukhumvit-Bold", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";

$font-family-sukhumvit-text: "Sukhumvit-Text", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";

