.svg-icon {
    display: inline-block;
    width: $font-size-base;
    height: $font-size-base;
    fill: $gray-500;
    vertical-align: text-top;

    @include m('large') {
        width: 24px;
        height: 24px;

        use {
            svg {
                width: 24px;
                height: 24px;
            }
        }
    }
}