.content {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    z-index: $zindex-content;
    margin: auto;
    @include make-container-max-widths();

    @include e('main') {
        width: 100%;
        
        @include media-breakpoint-up(lg) {
            flex: 0 0 65%;
            max-width: 65%;
        }

        @include media-breakpoint-up(xl) {
            flex: 0 0 70%;
            max-width: 70%;
        }
    }

    @include e('social') {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid $border-color;
        padding-bottom: $spacer;
        margin-left: $spacer;
        margin-right: $spacer;

        @include e('count') {
            font-size: 1.5rem;
            color: $primary;
            margin-bottom: 0;

            span {
                display: block;
                font-size: 0.875rem;
                color: $text-muted;
            }

            @include media-breakpoint-up(lg) {
                span {
                    display: inline-block;;
                    font-size: 1.125rem;
                }
            }
        }

        @include e('share') {
            margin-left: auto;
        }
    }

    @include e('read') {
        padding: ($spacer * 2) $spacer;

        p:last-child {
            margin-bottom: 0;
        }

        img {
            max-width: 100%;
            height: auto;
        }

        iframe {
            border: none;
        }
    }

    @include e('aside') {
        width: 100%;
        
        @include media-breakpoint-up(lg) {
            flex: 0 0 35%;
            max-width: 35%;
            padding-right: 0;
            padding-left: 0;
        }

        @include media-breakpoint-up(xl) {
            flex: 0 0 30%;
            max-width: 30%;
        }
    }

    @include e('tag') {
        padding: ($spacer * 1.5) $spacer;

        h4 {
            @include media-breakpoint-up(lg) {
                display: inline-block;
                margin-right: $spacer;
                margin-bottom: 0;
            }
        }

        @include e('content') {
            font-size: 0;
            
            @include media-breakpoint-up(lg) {
                display: inline-block;
                vertical-align: middle;
            }
        }
    }

    @include e('comment') {
        flex: 0 0 100%;
        max-width: 100%;
        border-top: 5px solid $gray-300;
        margin: $spacer 0;
        padding-top: 25px;
    }
}

.center {
    text-align: center;
}