.hero-box {
    display: flex;
    align-items: center;
    height: 100%;

    @include e('content') {
        width: 100%;
        height: 200px;

        @include media-breakpoint-up(lg) {
            height: 250px;
        }
    }

    @include e('ad') {
        display: none;
        margin-left: 15px;

        @include media-breakpoint-up(lg) {
            display: inline-block;
        }
    }
}

.caster-box {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: $font-size-lg;
    color: $white;
    text-align: center;
    text-indent: -999em;
    width: 100%;
    height: 100%;
    border: 1px solid $border-color;

    &:hover {
        color: $white;
        text-decoration: none;
    }
}