.breadcrumb-box {
    position: relative;
    z-index: 5;
    margin: auto;
    @include make-container-max-widths();

    @include media-breakpoint-down(md) {
        display: none;
    }
}

.breadcrumb {
    font-size: $font-size-sm;
    margin-bottom: 0;

    @include m('section') {
        a {
            color: $text-muted;
        }
    }
}