.hot-tag-desktop {
    @include media-breakpoint-down(md) {
        display: none;
    }

    @include media-breakpoint-up(lg) {
        max-width: 800px;
        max-height: 28px;
        overflow: hidden;
    }

    @include media-breakpoint-up(xl) {
        max-width: 1200px;
    }
}

.hot-tag-mobile {
    background-color: $white;
    position: relative;
    z-index: 10;
    margin: 0 auto;
    padding: 0 $spacer $spacer;
    @include make-container-max-widths();

    @include media-breakpoint-up(md) {
        display: none;
    }
}

.tag-box {
    transition-duration: 0.2s;

    @include m('hidden') {
        opacity: 0;
    }

    h5 {
        background-color: $primary;
        position: relative;
        display: inline-block;
        font-size: $font-size-sm;
        color: $white;
        height: 28px;
        line-height: 28px;
        margin-right: 1rem;
        margin-bottom: 0;
        padding-left: .45rem;
        padding-right: .25rem;

        span {
            position: relative;
            z-index: 2;
        }

        &::after {
            content: '';
            background-color: $primary;
            position: absolute;
            top: 4px;
            right: -10px;  
            width: 20px;
            height: 20px;
            transform: rotate(-45deg);
        }
    }

    @include e('item') {
        display: inline-block;
        color: $dark;
        padding: .25rem .45rem;
        @include transition($btn-transition);

        &:hover {
            text-decoration: none;
        }

        @include media-breakpoint-up(lg) {
            color: $white;

            &:hover {
                color: darken($white, 25%) ;
            }
        }
    }
}