.ribbon {
    position: absolute;
    left: 0;
    top: 0;
	z-index: 1;
    width: 45px;
    height: 45px;
    overflow: hidden;
    
    span {
		background: $red;
        position: absolute;
        top: 4px;
        left: -25px;
        display: block;
        font-size: 10px;
        font-weight: bold;
        color: #FFF;
		text-align: center;
		width: 80px;
        line-height: 20px;
        transform: rotate(-45deg);	
        
        &::before {
            content: "";
            position: absolute;
            left: 0px;
            top: 100%;
            z-index: -1;
            border-right: 2px solid transparent;
            border-bottom: 2px solid transparent;	
        }
        
        &::after {
            content: "";
            position: absolute;
            right: 0px;
            top: 100%;
            z-index: -1;
            border-left: 2px solid transparent;
            border-bottom: 2px solid transparent;
        }
    }

    @include m('no1') {
        span {
            background: $red;
        }
    }

    @include m('no2') {
        span {
            background: $yellow;
        }
    }

    @include m('no3') {
        span {
            background: $green;
        }
    }

    @include m('no4') {
        span {
            background: $cyan;
        }
    }

    @include m('no5') {
        span {
            background: $purple;
        }
    }
}