.subscribe-box {
    background-color: $white;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-top: $spacer;
    padding-bottom: $spacer;
    padding-left: $spacer;
    padding-right: $spacer;

    @include media-breakpoint-up(sm) {
        
    }

    @include media-breakpoint-up(lg) {
        background-color: transparent;
        border-top: 1px solid darken($border-color, 50%);
    }

    @include e('channel') {
        display: flex;
        min-width: 180px;

        @include media-breakpoint-up(md) {
            flex-direction: row
        }

        @include e('thumb') {
            width: 50px;
            margin-right: ($spacer * .75);
        }
    
        @include e('body') {
            display: flex;
            align-items: center;

            h3 {
                font-size: $h3-font-size;
                margin-bottom: 0;
            }

            @include media-breakpoint-up(lg) {
                a {
                    color: $white;
                }
            }
        }

        @include e('meta') {
            font-size: $font-size-sm;
            color: $text-muted;
        }
    }
}