.quick-nav {
    display: flex;
    align-items: center;
    transition-duration: 0.2s;
    margin-left: auto;

    @include m('hidden') {
        opacity: 0;
    }

    h5 {
        display: inline-block;
        margin: 0 10px 0 0;
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    li {
        background-color: $white;
        display: inline-block;
        font-size: $font-size-sm;

        &:hover {
            background-color: $gray-200;
        }

        &.active {
            background-color: $gray-500;
            color: $white;
            padding: $input-btn-padding-y $input-btn-padding-x;
        }
    }

    a {
        display: block;
        text-decoration: none;
        padding: $input-btn-padding-y $input-btn-padding-x;
    }
}

.nav-menu {
    .quick-nav {
        position: relative;
        min-width: 180px;
        height: 36px;

        @include media-breakpoint-up(sm) {
            min-width: 200px;
        }

        h5 {
            background: $primary url("../images/angle-down-white.svg") no-repeat center;
            background-size: 14px;
            position: absolute;
            top: 0;
            right: 0;
            z-index: 3;    
            width: 36px;
            height: 36px;  
            text-indent: -999em;
            margin: 0;
        }

        ul {
            position: absolute;
            top: 0;
            z-index: 2;
            width: 100%;
            border: 1px solid $primary;
        }

        li {
            display: none;
            border-top: 1px solid #eee;

            &.active {
                background-color: $white;
                display: list-item;
                color: $primary;
                border-bottom-width: 0;
                padding: $input-btn-padding-y $input-btn-padding-x;
            }
        }

        a {
            padding-right: 2.5em;
        }

        &.expanded {
            li {
                display: list-item;

                &.active {

                    a {
                        background-color: $gray-200;
                    }
                }
            }
        }
    }
}